import { makeObservable, observable, action } from 'mobx';
import { PMDataVM } from '../../../single/form/PMDataVM';
import { PMData } from '../../../single/model/PMData';
import * as projectUtil from '../ProjectUtil'
import * as projectVMUtil from '../ProjectVMUtil'
import * as util from './ProjectCompVMUtil'
import * as pvt from '../ProjectViewType'
import MassResultCompView from '../../mass/result/MassResultCompView';
import TypicalLevelSettingView from '../../setting/typicallevel/TypicalLevelSettingView';
import Phase_DictView from '../../phase/Phase_DictView';
import RebarMass_DictView from '../../mass/rebar/RebarMass_DictView';
import LevelSettingDataView from '../../setting/level/LevelSettingDataView';

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export const pmData = () => {
  return PMData.Instance;
}


export default class ProjectCompVM {
  constructor() {
    makeObservable(this, {
      projectVM: observable,
      projectViewType: observable
    })
  }

  _id
  get id() {
    return this._id;
  }
  set id(v) {
    this._id = v;
    projectUtil.getById(v, (item) => {
      this.ProjectVM = projectVMUtil.get(item);
    })
  }

  projectVM = undefined
  get ProjectVM() {
    return this.projectVM;
  }
  set ProjectVM(v) {
    this.projectVM = v;

    pmDataVM().CurrentProject = v;

    var list = [v];
    pmDataVM().Project_DictVM.Items = list;
    pmData().Phase_Dict = v.ModelItem.Phase_Dict;

    var massResComp = this.MassResultCompVM;
    massResComp.ProjectVMStorageList = list;
    massResComp.ProjectSelectVM.InitialItems = list;

    var setItemsFuncs = () => massResComp.PhaseVMStorageList = phase_DictVM.Items;

    var phase_DictVM = v.Phase_DictVM;
    if (phase_DictVM.Items) {
      setItemsFuncs();
    } else {
      phase_DictVM.OnGetItemsDone_Funcs.push(() => {
        setItemsFuncs();
      })
    }
  }

  massResultCompVM
  get MassResultCompVM() {
    if (!this.massResultCompVM) {
      var obj = this.massResultCompVM = util.GetMassResultCompVM(this);
      pmDataVM().MassResultCompVM = obj;
    }
    return this.massResultCompVM;
  }

  projectViewType = undefined
  get ProjectViewType() {
    if (!this.projectViewType) {
      this.projectViewType = pmDataVM().ProjectViewType;
    }
    return this.projectViewType;
  } set ProjectViewType(v) {
    this.projectViewType = v;
  }

  get ViewElement() {
    if (!this.ProjectVM) return undefined;

    switch (this.ProjectViewType) {
      case pvt.mass:
        return <div className='massresult__comp__wrapper'>
          <MassResultCompView store={this.MassResultCompVM} />
        </div>
      case pvt.typicallevel:
        return <>
          {this.ProjectVM.LevelSetting ?
            <LevelSettingDataView store={this.ProjectVM.LevelSetting.Data} />
            : ''}

          {this.ProjectVM.TypicalLevelSetting_ForEdit ?
            <TypicalLevelSettingView store={this.ProjectVM.TypicalLevelSetting_ForEdit} />
            : ''}
        </>
      case pvt.phase:
        return <div className='phase__comp__wrapper'>
          <Phase_DictView store={this.ProjectVM.Phase_DictVM} />
        </div>
      case pvt.inputdata:
        return <RebarMass_DictView store={this.ProjectVM.RebarMass_Dict} />
    }
  }
}