import { PMData } from "../../../single/model/PMData";
import MassRow from "./MassRow";
import * as multiSelectUtil from '../../../../select/MultiSelectVMUtil';
import * as selectUtil from '../../../../select/SelectVMUtil';
import * as rrlf_lt from './location/RRLF_LocationType';
import * as mgiUtil from './rowgroup/MassGroupItemUtil'
import * as mgi_iUtil from './rowgroup/MGI_ItemUtil'
import * as mgii_ct from './rowgroup/MGI_I_CreateType'
import * as genUtil from '../../../../../util/generalUtil';
import { EntFilter } from "../../filter/EntFilter";
import MassGroupComponent from "./rowgroup/MassGroupComponent";
import * as fvt from '../../filter/FilterValueType'
import * as sft from '../../filter/StringFilterType'
import * as mif from '../item/MassItemField'
import MassRow_ToolElement from "./tool/MassRow_ToolElement";
import * as trs from '../../../../text/TextRenderState';
import * as cellUtil from './MassCellUtil'
import MassCell from "./MassCell";

export const pmData = () => {
  return PMData.Instance;
}

export const get = (q, dict) => {
  var qI = new MassRow();
  qI.Name = q;
  qI.Dict = dict;
  return qI;
}

export const GetRank = (q) => {
  return q.SupItem ? q.SupItem.Rank + 1 : 0;
}

export const GetStyle = (q) => {
  return {
    marginLeft: q.Rank * 20
  }
}

export const GetIsHaveSub = (q) => {
  return q.SubItems.length !== 0;
}

export const GetToggleButtonContent = (q) => {
  return q.IsShowSub ? '-' : '+';
}

export const GetFieldSelectVM = (q) => {
  var storageList = pmData().MIFs_ForMassGroup;
  var obj = selectUtil.get(storageList, q.Field);
  obj.Placeholder = 'chn nhm'

  obj.IsDisabled = !q.IsCreatedByUser

  obj.OnSelectedItemChanged = (v) => q.TempField = v;
  return obj;
}

export const GetSubFieldSelectVM = (q) => {
  var storageList = pmData().MIFs_ForMassGroup;
  var obj = multiSelectUtil.get(storageList, undefined);
  obj.Placeholder = 'chn phn nhm'
  obj.OnSelectedItemChanged = (v) => q.TempSubFields = v;
  return obj;
}

export const Order = (list) => {
  if (list.length === 0) return list;

  var firstRow = list[0]
  var mgi = firstRow.OrderMGI;
  if (!mgi.IsHaveValueOrder) return list;

  var orderValues = mgi.OrderValues;
  return list.sort((a, b) => {
    return orderValues.indexOf(a.FullValue) - orderValues.indexOf(b.FullValue);
  });
}

export const Handle_Title = (q) => {
  var field = q.MassGroupItem.Field;
  if (field !== mif.level) {
    q.Title = q.Name;
  } else {
    // X l phn tiu  ca Row da trn Name v thng tin Tng in hnh
    var list = [];
    var handledCount = 0;
    var count = q.Cells.length;

    const handle_LS = (ls) => {
      if (ls.Description) {
        q.Title = ls.Description;
      } else {
        ls.OnGetLevelStorageList_Funcs.push(() => {
          q.Title = ls.Description;
        })
      }
    }

    const handle_Val = (val) => {
      handledCount++;
      list.push.apply(list, val);
      if (handledCount === count) {
        if (list.length === 1) {
          handle_LS(list[0]);
        } else {
          q.Title = q.Name;
        }
      }
    }

    q.Cells.forEach(x => {
      if (x.TLS_LevelGroups) {
        handle_Val(x.TLS_LevelGroups);
      } else {
        x.OnGet_TLS_LevelGroups_Funcs.push(() => {
          handle_Val(x.TLS_LevelGroups);
        })
      }
    })
  }
}

export const GetLocationSelectVM = (q) => {
  var obj = selectUtil.get([], undefined);
  obj.Placeholder = 'chn v tr'
  obj.TitleRender = (item) => {
    var marginLeft = item.Rank * 15;
    return <div
      style={{ marginLeft }}>
      {item.Title}
    </div>
  }
  obj.OnSelectedItemChanged = (v) => {
    var factory = q.ReplaceFactory;
    factory.ReferenceRow = v;
  }
  return obj;
}

export const GetOriginal_SubMassGroupItem = (q) => {
  var subItems = q.SubItems;
  if (subItems.length === 0) return undefined;
  return subItems[0].MassGroupItem;
}

export const GetReplace_SubMassGroupItem = (q) => {
  var subItems = q.SubItems;
  if (subItems.length === 0) return undefined;
  return subItems[0].ReplaceMGI;
}

export const GetSubFields = (q) => {
  var subMGI = q.SubMassGroupItem;
  if (!subMGI) return [];
  var mgis = subMGI.SupComponent.Items;

  var lastValidIndex = -1;
  var isValidHandle = true
  mgis.forEach((x, i) => {
    if (isValidHandle && x.HaveMassItemSubComponents.length === 0) {
      lastValidIndex = i;
    } else {
      isValidHandle = false;
    }
  })

  var currentIndex = mgis.indexOf(q.MassGroupItem);
  if (lastValidIndex === -1) return [subMGI.Field];
  var fields = mgis.filter((x, i) =>
    (currentIndex === -1 || currentIndex < i) &&
    i <= lastValidIndex)
    .map(x => x.Field);
  return fields;
}

// MassGroupItem ph  t u mc c to mi
export const CreateSubMassGroupItem = (q) => {
  var filter = new EntFilter();
  filter.ValueType = fvt.bystring;
  filter.StringFilterType = sft.byequal;
  filter.Values = [q.FullValue];

  var supMgi = q.MassGroupItem;

  var mgc = undefined;
  var mgcIndex = supMgi.SubComponents
    .findIndex(x => genUtil.IsSameValues(x.Filter.Values, filter.Values));

  if (mgcIndex !== -1) {
    mgc = supMgi.SubComponents[mgcIndex]
  } else {
    mgc = new MassGroupComponent();
    mgc.SubItem = supMgi;
    mgc.Filter = filter;
    supMgi.SubComponents.push(mgc);
  }

  var mgi = mgiUtil.get(null, null, mgc);
  mgc.Items = [mgi];

  q.SubMassGroupItem = mgi;
  return mgi;
}

export const Add = (supRow, table) => {
  var mgi = supRow ?
    (supRow.SubMassGroupItem ? supRow.SubMassGroupItem :
      CreateSubMassGroupItem(supRow))
    : table.MainMGI;

  var mgi_I = mgi_iUtil.create(mgi, mgii_ct.byuser);

  // Thm trng SupValue  xc nh SupRow 
  // trong trng hp SupRow.CurrentList.length !== 0
  mgi_I.SupValue = supRow?.Name;

  mgi.Items.push(mgi_I);

  var row = get(mgi_I.Value, table);
  row.FullValue = mgi_I.Value;
  row.IsCreatedByUser = true;

  // Ly thng tin Cell v hin ti ang b li khi render
  var obj = row.Cells

  row.MassGroupItem = mgi;

  row.SupItem = supRow;
  row.CurrentList.push(row);

  row.Editable_TitleVM.RenderState = trs.edit;

  return row;
}

export const GetOn_HandleTotal_Cells_Done_Dict_Funcs = (q) => {
  var obj = [];
  return obj;
}

export const Get_Original_SupRow = (q) => {
  if (q.IsGet_Original_SupRow) return undefined;
  q.IsGet_Original_SupRow = true;

  return q.SupItem;
}

export const Get_Original_Full_SupRows = (q) => {
  var list = [];
  var supItem = q.Original_SupRow;

  while (supItem) {
    list.push(supItem);
    supItem = supItem.Original_SupRow;
  }

  list.reverse();
  return list;
}

// Aditional Infomation
export const Get_IsHaveAdditionInformation = (q) => {
  return q.IsReplaced ? true : false
}

export const Get_AdditionInformation = (q) => {
  var obj = q.Original_Full_SupRows.reduce((a, x, i) => {
    if (i != 0) {
      a += ' > '
    }
    a += x.Title;
    return a;
  }, '')

  return obj;
}

export const Get_ToolElement = (q) => {
  var qI = new MassRow_ToolElement();
  qI.Dict = q;
  return qI;
}

// Cells
export const Get_Cells = (q) => {
  if (!q.IsCreatedByUser) return [];

  var table = q.Table;

  var list = table.Columns.map(x => {
    var qI = new MassCell();
    qI.Column = x;
    qI.Row = q;
    qI.Dict = table;

    table.Cells.push(qI);

    return qI;
  });

  return list;
}

export const GetIsShow_RawValue = (q) => {
  return !q.IsCreatedByUser && q.RawValue !== q.Title;
}