import { Component } from "react";
import { observer } from 'mobx-react';
import MassCell_Value from "./MassCell_Value";

class MassCell_ValueView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassCell_Value();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    if (data.Dict.Row.Title === 'DM 6, 7'){
      debugger
      var value = data.Value;
    }

    return (
      <>
        {data.Value}
      </>
    )
  }
}

export default observer(MassCell_ValueView)