import { makeObservable, observable, action } from 'mobx';
import * as util from './LevelSettingDataVMUtil'
import { ControlOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import * as itemUtil from './item/LS_ItemVMUtil'

export class LevelSettingDataVM {
  constructor() {
    makeObservable(this, {
      items: observable,
    })
  }

  items = undefined
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this);
    }
    return this.items;
  }

  AddItem = () => {
    var item = itemUtil.get(this.ModelItem.AddItem(), this);
    this.Items.push(item);

    return item;
  }
}