import { makeObservable, observable, action } from 'mobx';
import * as mif from '../item/MassItemField'
import MassColumn from './MassColumn';
import * as colUtil from './MassColumnUtil'
import * as rowUtil from './MassRowUtil'
import * as cellUtil from './MassCellUtil'
import * as util from './MassTableUtil'
import { PMData } from '../../../single/model/PMData';
import * as filterUtil from '../../filter/EntFilterUtil'
import * as textUtil from '../../../../text/TextVMUtil'
import * as api from '../../../../../util/apiUtil'
import * as st from '../../setting/base/SettingType'
import { message } from 'antd';
import * as mgsUtil from '../../setting/massGroup/MassGroupSettingUtil'
import * as mgsVMUtil from '../../setting/massGroup/MassGroupSettingVMUtil'
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as compUtil from './rowgroup/MassGroupComponentUtil'
import * as exportUtil from '../../../../export/csv/CSVExportUtil';
import dayjs from 'dayjs';
import * as eeUtil from '../../../../export/excel/ExcelExportUtil';
import * as wbUtil from './export/MT_WorkbookUtil';
import * as mgi_st from './MassGroupInput_StateType'
import InputView from '../../../../input/InputView';
import * as selectUtil from '../../../../select/SelectVMUtil';
import SelectView from '../../../../select/SelectView';
import Modal from 'antd/lib/modal/Modal';
import * as genUtil from '../../../../../util/generalUtil'

export const pmData = () => {
  return PMData.Instance;
}

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export default class MassTable {
  constructor() {
    makeObservable(this, {
      IsValidTable: observable,
      Columns: observable,
      MainRows: observable,
      Cells: observable,
      columnField: observable,
      IsOnSaveMassGroup: observable,
      MassGroupInput_State: observable,
      Is_Override_MassGroup_Modal_Open: observable
    })
  }


  items
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;

    if (v && v.length !== 0) {
      this.Build();
      this.IsValidTable = true;
    }
    else {
      this.IsValidTable = false;
    }
  }

  IsValidTable = false

  columnField = mif.Project
  get ColumnField() {
    return this.columnField;
  }
  set ColumnField(v) {
    this.columnField = v;
  }

  massGroupSetting
  get MassGroupSetting() {
    return this.massGroupSetting;
  }
  set MassGroupSetting(v) {
    this.massGroupSetting = v;
    if (!v) {
      this.MainMGC = v;
    } else {
      this.MainMGC = compUtil.Clone(v.Data.MassGroupComponent);
    }
  }

  mainMGC
  get MainMGC() {
    if (!this.mainMGC) {
      this.mainMGC = util.GetMainMGC(this);
    }
    return this.mainMGC;
  }
  set MainMGC(v) {
    this.mainMGC = v;
    this.Init();
    if (!v) {
      this.MainMGI = undefined;
    } else {
      this.MainMGI = util.GetMainMGI(this);
    }
  }

  mainMGI
  get MainMGI() {
    if (!this.mainMGI) {
      this.mainMGI = util.GetMainMGI(this);
    }
    return this.mainMGI;
  }
  set MainMGI(v) {
    this.mainMGI = v;
  }

  Columns = undefined
  FullRows = undefined
  MainRows = undefined
  Cells = undefined

  Init = () => {
    this.Columns = [];
    this.MainRows = [];
    this.FullRows = [];
    this.Cells = [];

    // Reset cc dng to bi ngi dng
    this.CreatedByUser_Rows = []
    this.Handled_CreatedByUser_Rows = []
  }

  get AllRows() {
    return util.GetAllRows(this);
  }

  GetColumn = (v) => {
    var list = this.Columns;
    var item = undefined;
    var index = list.findIndex(x => x.Name === v);
    if (index === -1) {
      item = colUtil.get(v, this);
      list.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  GetRow = (v, supItem) => {
    var list = undefined;
    if (supItem) {
      list = supItem.SubItems;
    } else {
      list = this.MainRows;
    }

    var item = undefined;
    var index = list.findIndex(x => x.Name === v);
    if (index === -1) {
      item = rowUtil.get(v, this);
      item.SupItem = supItem;

      list.push(item);
      this.FullRows.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  GetCell = (col, row, canAdd = true) => {
    var list = this.Cells;
    var item = undefined;
    var index = list.findIndex(x => x.Column === col && x.Row === row);
    if (index === -1) {
      if (!canAdd) return undefined;

      item = cellUtil.get(col, row, this);
      list.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }


  Build = () => {
    var colField = this.ColumnField;

    if (colField === mif.Phase) {
      pmData().MassResultComp.Phases.forEach(x => {
        var col = this.GetColumn(mif.GetValueString(x, colField));
      })
    }

    this.Items.forEach(x => {
      switch (colField) {
        case mif.Phase:
          x.ValidPhasesByInput.forEach(y => {
            this.HandleItem(x, y);
          })
          break;
        default:
          this.HandleItem(x, x.GetValue(colField));
          break;
      }
    })

    // Lc no cng AsyncData
    // if (!this.IsHaveAsyncData) {
    //   this.UpdateData();
    // }
  }

  //IsHaveAsyncData = false;
  IsOnSaveMassGroup = false;

  HandledItems = []
  HandledItemPhases = []

  OnSetHandledRatio_Funcs = []

  handledRatio
  get HandledRatio() {
    return this.handledRatio;
  } set HandledRatio(v) {
    this.handledRatio = v;
    this.OnSetHandledRatio_Funcs.forEach(x => x(v));
  }

  AddHandledItems = (item) => {
    if (this.ColumnField !== mif.Phase) {
      this.HandledItems.push(item);
    } else {
      var list = this.HandledItemPhases;
      var index = list.findIndex(x => x.Item === item);
      var obj = undefined;
      if (index === -1) {
        obj = {
          Item: item,
          Count: 0
        };
        list.push(obj);
      } else {
        obj = list[index];
      }
      obj.Count++;

      if (obj.Count === item.ValidPhasesByInput.length) {
        this.HandledItems.push(item);
      }
    }

    this.HandledRatio = this.HandledItems.length / this.Items.length;

    if (this.HandledItems.length === this.Items.length) {
      this.UpdateData();
      this.HandledItems = [];
    }
  }

  UpdateData = () => {
    this.MainRows.forEach(x => x.UpdateData());
    this.MainRows = rowUtil.Order(this.MainRows);

    this.Cells.forEach(x => x.UpdateData());

    this.IsUpdateDataDone = true;
  }

  OnIsUpdateDataDone_Funcs = []

  set IsUpdateDataDone(v) {
    this.OnIsUpdateDataDone_Funcs.forEach(x => x());
  }

  HandleItem = (item, colFieldValue, mgc, initialRow) => {
    var col =
      this.GetColumn(mif.GetValueString(colFieldValue, this.ColumnField));
    var rows = initialRow ? [initialRow] : [];
    var mgc = mgc ? mgc : this.MainMGC;

    this.HandleRowFieldIndex(item, col, rows, mgc, 0);
  }

  // X l to Row vi gi tr tng ng v Cell bn trong
  GetRow_Value_FieldIndex = (item, col, rows, val, mgc, rowFieldIndex) => {
    var mgis = mgc.Items;
    var itemCount = mgis.length;
    var mgi = mgis[rowFieldIndex];

    // Sup Row ca dng khi lng ang xt
    var lastRow = rows.length !== 0 ? rows[rows.length - 1] : undefined
    // mgi.SupRow = lastRow;
    mgi.AddSupRow(lastRow);

    var rowField = mgi.Field;
    if (!rowField) {
      this.AddHandledItems(item);
      return;
    }

    var valueString = mif.GetValueString(val, rowField);
    var fullValue = lastRow ? `${lastRow.FullValue}__${valueString}` : valueString;

    if (mgi.NotIncludeValues.includes(fullValue)) {
      this.AddHandledItems(item);
      return;
    }

    var row = undefined;
    const addRow = (supRow) => {
      return this.Handle_AddRow(fullValue, supRow);
    }

    const handleRow = (row) => {
      // Gn thng tin gc vo Row
      row.RawValue = valueString;
      row.MassGroupItem = mgi;
      rows.push(row);

      var subMgcs = mgi.SubComponents;
      var subIndex = subMgcs.findIndex(x =>
        filterUtil.IsValid(x.Filter, fullValue));
      if (subIndex !== -1) {
        mgc = subMgcs[subIndex];
        mgc.IsHaveMassItem = true;

        rowFieldIndex = -1;
        itemCount = mgc.Items.length;

        row.SubMassGroupComponent = mgc;
      }

      var cell = this.GetCell(col, row);
      cell.Items.push(item);

      if (rowFieldIndex === itemCount - 1) {
        this.AddHandledItems(item);
      } else {
        this.HandleRowFieldIndex(item, col, rows, mgc, rowFieldIndex + 1);
      }
    }

    var mgiItem = mgi.GetItem(fullValue, valueString);
    var replaceMgi = mgiItem.NewMGI;
    if (replaceMgi) {
      // Ly thng tin SupName
      var supName = mgiItem.SupValue;

      // Thm Value-Title cho MassGroupItem
      replaceMgi.GetItem(fullValue, valueString);

      // nh thng tin OriginSupRow c
      if (lastRow) {
        lastRow.Is_Original_SupRow = true;
      }

      this.Handle_ReplaceMGI(replaceMgi, supName, addRow, handleRow, lastRow, item);
    } else {
      row = addRow(lastRow);
      handleRow(row);
    }
  }

  Handle_AddRow = (fullValue, supRow) => {
    var r1 = this.GetRow(fullValue, supRow);
    r1.FullValue = fullValue;
    return r1;
  }

  Handle_ReplaceMGI = (replaceMgi, supName, addRow, handleRow, originalSupRow, massItem) => {
    var isGet_SupRow_Done

    // Ly SupRow ph hp c trong ReplaceMGI
    const getSupRow = () => {
      var list = replaceMgi.SupRows;
      var index = list.findIndex(x => x.Name === supName);
      if (index !== -1) {
        isGet_SupRow_Done = true;
        return list[index]
      }
      return undefined;
    }

    // Thm Row vo SupRow
    const awaitGetSupRowForAdd = (supRow) => {
      var r2 = addRow(supRow);
      r2.ReplaceMGI = replaceMgi;

      return r2;
    }

    // nh thng tin OriginSupRow
    const setOriginal_SupRow = (row) => {
      row.Original_SupRow = originalSupRow;
      row.IsReplaced = true;
    }

    // Tn hp cc Phng thc  trn
    const fullFunc = () => {
      var sr = getSupRow();
      if (sr) {
        var r = awaitGetSupRowForAdd(sr);
        setOriginal_SupRow(r);
        handleRow(r);
      }
    }

    fullFunc();
    genUtil.Add_2(
      replaceMgi.On_AddSupRow_Func_Dict,
      () => {
        if (!isGet_SupRow_Done && replaceMgi.NewRow?.Name === supName) {
          fullFunc();
        }
      },
      false);

    // var row = undefined
    // if (replaceMgi.IsFirstSetSupRow) {
    //   row = awaitGetSupRowForAdd();
    //   setOriginal_SupRow(row);
    //   handleRow(row);
    // } else {
    //   replaceMgi.OnIsFirstSetSupRow_Funcs.push(() => {
    //     row = awaitGetSupRowForAdd();
    //     setOriginal_SupRow(row);
    //     handleRow(row);
    //   })
    // }
  }

  HandleRowFieldIndex = (item, col, rows, mgc, rowFieldIndex) => {
    var rowField = mgc.Items[rowFieldIndex].Field;

    var val = item.GetValue(rowField, (val) =>
      this.GetRow_Value_FieldIndex(item, col, rows, val, mgc, rowFieldIndex));
    if (!Array.isArray(val)) {
      this.GetRow_Value_FieldIndex(item, col, rows, val, mgc, rowFieldIndex);
    }

    // Lc no cng c AsyncData
    // else {
    //   this.IsHaveAsyncData = true;
    // }
  }

  // MassGroupInput
  get Reference_MassGroupSetting_SelectVM() {
    return this.Dict.Dict.ViewItem.MassGroupSettingSelectVM;
  }

  SaveMassGroup = () => {
    // Init MassGroup StorageList
    var refSelectVM = this.Reference_MassGroupSetting_SelectVM;
    var obj = this.MassGroupSelectVM;
    obj.Items = [...refSelectVM.Items];
    obj.InitialItem = refSelectVM.SelectedItem;

    this.MassGroupInput_State = mgi_st.select
    this.IsOnSaveMassGroup = true;
  }

  MassGroupInput_State = undefined

  Toggle_MassGroupInput_State = () => {
    switch (this.MassGroupInput_State) {
      case mgi_st.select:
        this.MassGroupInput_State = mgi_st.input
        break;
      case mgi_st.input:
        this.MassGroupInput_State = mgi_st.select
        break;
    }
  }

  get MassGroupInput_Toggle_Classname() {
    switch (this.MassGroupInput_State) {
      case mgi_st.select:
        return 'btn-warning';
      case mgi_st.input:
        return 'btn-success';
    }
  }

  get MassGroupInput_Toggle_Content() {
    switch (this.MassGroupInput_State) {
      case mgi_st.select:
        return 'sel';
      case mgi_st.input:
        return 'inp';
    }
  }

  massGroupName = undefined
  get MassGroupName() {
    if (!this.massGroupName) {
      this.massGroupName = util.GetMassGroupName(this);
    }
    return this.massGroupName;
  }
  set MassGroupName(v) {
    this.massGroupName = v;
  }

  massGroupNameVM
  get MassGroupNameVM() {
    if (!this.massGroupNameVM) {
      var obj = this.massGroupNameVM =
        textUtil.get(this.MassGroupName, 'Tn set khi lng');
      obj.onSetContent = (v) => this.MassGroupName = v;
    }
    return this.massGroupNameVM;
  }

  massGroupSelectVM
  get MassGroupSelectVM() {
    if (!this.massGroupSelectVM) {
      var obj = this.massGroupSelectVM = selectUtil.get([], undefined);
      obj.Placeholder = 'B set khi lng';
      obj.OnSelectedItemChanged = (v) => {
        this.MassGroupName = v?.Name
      };
    }
    return this.massGroupSelectVM;
  }

  get MassGroupInput_Element() {
    switch (this.MassGroupInput_State) {
      case mgi_st.select:
        var obj = this.MassGroupSelectVM;
        var list = obj.Items;
        var index = list.findIndex(x => x.Name === this.MassGroupName);
        obj.InitialItem = index !== -1 ?
          list[index] : undefined
        return <SelectView store={obj} />
      case mgi_st.input:
        var obj = this.MassGroupNameVM;
        obj.Content = this.MassGroupName;
        return <InputView store={obj} />
    }
  }

  MassGroupInputOK = () => {
    if (!this.MassGroupName || this.MassGroupName.length === 0) {
      switch (this.MassGroupInput_State) {
        case mgi_st.select:
          message.warning('Vui lng chn b thit lp!')
          return;
        case mgi_st.input:
          message.warning('Vui lng nhp tn thit lp!')
          return;
      }
    }

    var needValidateForOveride = true;
    var items = pmData().MassGroupSetting_Dict.Items;
    var index = items.findIndex(x => x.Name === this.MassGroupName);
    var _id = undefined;
    if (index !== -1) {
      _id = items[index]._id;

      if (this.MassGroupInput_State === mgi_st.input) {
        needValidateForOveride = false;
      }
    }

    const handle_UpdateMassGroup = () => {
      var apiData = {
        _id,
        name: this.MassGroupName,
        settingType: st.massgroup,
        data: {
          massGroupComponent: this.MainMGC.ApiData
        }
      };

      var url = `https://api.bimdev.vn/api/hbs/mg`;

      var isNew = _id ? false : true;
      var command = isNew ? 'add' : 'edit';
      const apiFunc = () => !isNew ? api.put(`${url}/${command}`, apiData) :
        api.post(`${url}/${command}`, apiData);

      apiFunc().then(response => response.data)
        .then(data => {
          var refVM = this.Reference_MassGroupSetting_SelectVM

          var dict = pmData().MassGroupSetting_Dict;
          var item = mgsUtil.get(data, dict);
          var dictVM = pmDataVM().MassGroupSetting_DictVM;
          var itemVM = mgsVMUtil.get(item, dictVM);

          if (isNew) {
            dict.Items.push(item);
            dictVM.Items.push(itemVM);

            refVM.Items.push(itemVM);
          } else {
            dict.Items[index] = item;
            dictVM.Items[index] = item;

            refVM.Items[index] = itemVM;
            // Refresh li selected item nu trng vi i tng update
            if (refVM.SelectedIndex === index) {
              refVM.InitialItem = itemVM;
            }
          }

          message.success(`${command} massgroup successfully`);
        })

      this.IsOnSaveMassGroup = false;
      this.MassGroupNameVM.Content = util.GetMassGroupName(this);
    }

    if (needValidateForOveride) {
      handle_UpdateMassGroup();
    } else {
      this.Override_MassGroup_Modal_OKFunc =
        () => handle_UpdateMassGroup();

      this.Is_Override_MassGroup_Modal_Open = true;
    }
  }

  Is_Override_MassGroup_Modal_Open = false

  get Override_MassGroup_ModalElement() {
    var title = 'Cnh bo'
    var content = 'Thit lp  c sn, bn c mun ghi  khng?'

    const modalCancel = () =>
      this.Is_Override_MassGroup_Modal_Open = false

    const modalOK = () => {
      this.Is_Override_MassGroup_Modal_Open = false
      this.Override_MassGroup_Modal_OKFunc();
    }

    return <Modal title={title}
      visible={this.Is_Override_MassGroup_Modal_Open}
      onOk={modalOK} onCancel={modalCancel}>
      {content}
    </Modal>
  }

  MassGroupInputCancel = () => {
    this.IsOnSaveMassGroup = false;
    this.MassGroupNameVM.Content = util.GetMassGroupName(this);
  }

  AddMainRow = () => {
    return rowUtil.Add(undefined, this);
  }

  // Excel Export
  get ExportTitle() {
    var text = pmData().CurrentProject?.Name;
    text = text ? text + '_' : '';

    if (this.ColumnField === mif.Phase) {
      text += 'phase_'
    }

    const formatedDate = dayjs(Date.now()).format("YYMMDD");
    text += `masstable_${formatedDate}`
    return text;
  }

  get ExcelWorkbook() {
    return wbUtil.get(this).ResultData;
  }

  excelExport
  get ExcelExport() {
    if (!this.excelExport) {
      this.excelExport = eeUtil.get(
        undefined, undefined,
        () => this.ExportTitle,
        () => this.ExcelWorkbook);
    }
    return this.excelExport;
  }

  Export = () => {
    this.ExcelExport.Export();
  }

  // Lu tr cc Row to bi ngi dng
  // Trc mt dng  x l li khng update TotalValue
  // trong ln u tin truy xut bng khi lng
  CreatedByUser_Rows
  Handled_CreatedByUser_Rows

  Add_Handled_CreatedByUser_Row = (row) => {
    this.Handled_CreatedByUser_Rows.push(row);

    if (this.CreatedByUser_Rows.length === this.Handled_CreatedByUser_Rows.length) {
      // X l TotalValue thnh cng vi tt c cc CreatedByUser_Row
    }
  }
}