import * as mt from '../MassType'

export const Project = 'Project'
export const Phase = 'Phase'
export const Workpackage = 'Workpackage'
export const Value = 'Value'
export const Unit = 'Unit'
export const partition = 'partition'
export const rebarType = 'rebarType'
export const elementName = 'elementName'
export const MassType = 'MassType'
export const workItem = 'workItem'
export const level = 'level'
export const diameter = 'diameter'
export const block = 'block'
export const discipline = 'discipline'
export const zone = 'zone'
export const typename = 'typename'
export const materialName = 'materialName'
export const systemtype = 'systemtype'
export const systemclassification = 'systemclassification'
export const familyName = 'familyName'
export const size = 'size'
export const roomName = 'roomName'
export const roomNumber = 'roomNumber'

export const GetAll = () => {
  return [MassType, workItem, level, block, elementName, diameter]
}

export const GetForMassGroup = () => {
  return [
    MassType, workItem, level, block, zone,
    familyName, typename,
    elementName, materialName, roomName, roomNumber,
    systemtype, systemclassification, size

  ]
}

export const GetDirectiveFields = () => {
  return [Project, Workpackage, Value, Unit];
}

export const GetIdentifyFields = () => {
  return [elementName];
}

export const Get_MassInfo_Fields = () => {
  return [materialName]
}

export const GetFullFields = (massItem, field) => {
  switch (field) {
    case Project:
    case Value:
    case Unit:
    case MassType:
    case Phase:
      return {
        fields: [field],
        isFromElement: false
      };
    case workItem:
    case elementName:
    case rebarType:
    case partition:
    case level:
    case diameter:
    case block:
    case zone:
    case typename:
    case materialName:
    case roomName:
    case roomNumber:
    case systemtype:
    case systemclassification:
    case familyName:
    case size:
      var massType = massItem[MassType];
      if (Get_MassInfo_Fields().includes(field) ||
        massType === mt.rebar) {
        return {
          fields: ['Info', field],
          isFromElement: false
        };
      } else {
        var groupName = undefined;
        // kim tra cc phn nhm trong Element: identify, locate ...
        switch (field) {
          case workItem:
          case elementName:
          case typename:
          case systemtype:
          case systemclassification:
          case familyName:
            groupName = 'identify';
            break;
          case level:
          case block:
          case zone:
          case roomName:
          case roomNumber:
            groupName = 'locate';
            break;
          case size:
            groupName = 'geometry';
            break;
        }
        return {
          fields: ['Element', 'info', groupName, field],
          isFromElement: true
        }
      }
  }
}

export const GetValueString = (massFieldValue, field) => {
  var value = undefined;

  switch (field) {
    case Project:
    case Phase:
      value = massFieldValue.Name;
      break;
    case Workpackage:
      value = massFieldValue.FullName;
      break;
    default:
      value = massFieldValue;
      break;
  }

  return value ? value : 'undefined';
}

export const GetListForTypicalLevelFilter = () => {
  return [workItem, MassType, block]
}

export const GetDefaultListValue = (field) => {
  switch (field) {
    case workItem:
      return ['Dm', 'Sn', 'Ct', 'Vch'];
    default:
      return [];
  }
}