import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button } from "antd";
import { EditableTextVM } from "./EditableTextVM";
import './Text.css'

class EditableTextView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new EditableTextVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.Render()}
      </>
    )
  }
}

export default observer(EditableTextView)